import { createGlobalStyle } from "styled-components";
import { ThemeProvider } from "styled-components";
import { createContext, useState } from "react"; // ThemeContext  
import { Emotion } from "./Project/Emotion";
import { lightTheme, darkTheme, GlobalStyle } from "./Project/Themes";
import ToggleButton from "./Project/ToggleButton";
import { useEffect } from "react";

export const ThemeContext = createContext();

const Body = createGlobalStyle`
@font-face {
    font-family: 'NEXON Lv1 Gothic OTF';
    src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_20-04@2.1/NEXON Lv1 Gothic OTF.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}

::-webkit-scrollbar {
  opacity: 0;
  /* color: red; */
     /*   */
     /*   */
     

  };

::-webkit-scrollbar-thumb {
  background-color: gray;
  /* opacity: 0; */
 
     /*    */
     
  };

::-webkit-scrollbar-thumb:hover { 
  
    /*   hover  */
    
  };

body {  
  overflow-x: hidden;
  
* {
  font-family: "NEXON Lv1 Gothic OTF";
  box-sizing: border-box;
}
}
`;

function App() {
  const [isDarkTheme, setIsDarkTheme] = useState(false);

  useEffect(() => {
    const savedTheme = localStorage.getItem("theme");
    setIsDarkTheme(savedTheme === "darkTheme");
    console.log(localStorage);
  }, []); //      

  const toggleTheme = () => {
    const newTheme = isDarkTheme ? "lightTheme" : "darkTheme";
    setIsDarkTheme(!isDarkTheme);
    localStorage.setItem("theme", newTheme); //     
    console.log(newTheme);
  };

  return (
    <>
      <ThemeContext.Provider value={{ isDarkTheme }}>
        <ThemeProvider theme={isDarkTheme ? darkTheme : lightTheme}>
          <GlobalStyle />
          <div>
            <ToggleButton toggleTheme={toggleTheme}> </ToggleButton>
          </div>
          <Emotion />
          <Body />
        </ThemeProvider>
      </ThemeContext.Provider>
    </>
  );
}

export default App;
